<template>
    <div>
    <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <!--  -->
        <el-card>
            <!--  -->
            <el-alert title="" show-icon :closable="false" type="warning"></el-alert>
            <!--  -->
            <el-row class="cat_opt">
                <el-col>
                    <span></span>
                    <!--  -->
                    <!-- options -->
                    <el-cascader v-model="selectedKeys" :options="catelist" expand-trigger="hover"
                     :props="cateProps" @change="handleChange"></el-cascader>
                </el-col>
            </el-row>
                <!-- Tab -->
            <el-tabs v-model="activeName" @tab-click="handleTabClick">
                <!--  -->
                <el-tab-pane label="" name="many">
                    <el-button type="primary" size="mini" :disabled="isOk" @click="showDailog"></el-button>
                    <el-table :data='manyTableData' border stripe>
                        <el-table-column type="expand">
                            <template slot-scope="scope">
                                <el-tag closable v-for="(item,i) in scope.row.attr_vals" @close="removeTag(i,scope.row)" :key="i">{{item}}</el-tag>
                                  <!--  -->
                                <el-input class="input-new-tag"
                                    v-if="scope.row.inputVisible"
                                    v-model="scope.row.inputValue"
                                    ref="saveTagInput"
                                    size="small"
                                    @keyup.enter.native="handleInputConfirm(scope.row)"
                                    @blur="handleInputConfirm(scope.row)"
                                    >
                                </el-input>
                                <!--  -->
                                <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>
                            </template>
                        </el-table-column>
                        <el-table-column type="index"></el-table-column>
                        <el-table-column label="" prop="attr_name"></el-table-column>
                        <el-table-column label="" >
                            <template slot-scope="scope">
                                <el-button  size="mini" type="primary" 
                                icon="el-icon-edit" @click="showeditDailog(scope.row.attr_id)"></el-button>
                                <el-button size="mini" type="danger" icon="el-icon-delete" @click="removeParams(scope.row.attr_id)"></el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-tab-pane>
                <!--  -->
                <el-tab-pane label="" name="only">
                     <el-button type="primary" size="mini" :disabled="isOk"  @click="showDailog"></el-button>
                    <el-table :data='onlyTableData' border stripe>
                        <el-table-column type="expand">
                            <template slot-scope="scope">
                                <el-tag closable v-for="(item,i) in scope.row.attr_vals"  @close="removeTag(i,scope.row)" :key="i">{{item}}</el-tag>
                                <!--  -->
                                <el-input class="input-new-tag"
                                    v-if="scope.row.inputVisible"
                                    v-model="scope.row.inputValue"
                                    ref="saveTagInput"
                                    size="small"
                                    @keyup.enter.native="handleInputConfirm(scope.row)"
                                    @blur="handleInputConfirm(scope.row)"
                                    >
                                </el-input>
                                <!--  -->
                                <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>
                            </template>
                        </el-table-column>
                        <el-table-column type="index"></el-table-column>
                        <el-table-column label="" prop="attr_name"></el-table-column>
                        <el-table-column label="" >
                            <template slot-scope="scope">
                                <el-button :id="scope.row" size="mini" type="primary" 
                                icon="el-icon-edit" @click="showeditDailog(scope.row.attr_id)"></el-button>
                                <el-button size="mini" type="danger" icon="el-icon-delete" @click="removeParams(scope.row.attr_id)"></el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-tab-pane>
            </el-tabs>
            
        </el-card>
        <!--  -->
        <el-dialog :title="''+titleText" :visible.sync="adddialogVisible"
            width="30%" @close="DailogClosed">
           <el-form :model="addForm" :rules="addFormrules" ref="addFormRef" label-width="100px">
                <el-form-item :label="titleText" prop="attr_name">
                    <el-input v-model="addForm.attr_name"></el-input>
                </el-form-item>
           </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="adddialogVisible = false"> </el-button>
                <el-button type="primary" @click="addParams"> </el-button>
            </span>
        </el-dialog>
        <!--  -->
        <el-dialog :title="''+titleText" :visible.sync="editdialogVisible"
            width="30%" @close="editDailogClosed">
           <el-form :model="editForm" :rules="editFormrules" ref="editFormRef" label-width="100px">
                <el-form-item :label="titleText" prop="attr_name">
                    <el-input v-model="editForm.attr_name"></el-input>
                </el-form-item>
           </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="editdialogVisible = false"> </el-button>
                <el-button type="primary" @click="editParams"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
export default {
    data(){
        return{
            //
            catelist:[],
            //
            cateProps:{
                value:'cat_id',
                label:'cat_name',
                children:'children'
            },
            //
            selectedKeys:[],
            //Tab
            activeName:'many',
            //
            manyTableData:[],
            //
            onlyTableData:[],
            //
            adddialogVisible : false,
            editdialogVisible: false,
            //
            addForm:{
                attr_name:''
            },
            //
            editForm:{
                attr_name:''
            },
            //
            addFormrules:{
                attr_name:[
                    { required: true, message: '', trigger: 'blur' },
                ]   
            },
            //
            editFormrules:{
                attr_name:[
                    { required: true, message: '', trigger: 'blur' },
                ]   
            },
 
        }
    },
    created(){
        this.getCatelist();
    },
    methods:{
        //
        async getCatelist(){
            const {data:res} = await this.$http.get('categories');
            if(res.meta.status!=200){
                return this.$message.error('');
            }
            this.catelist = res.data;
           // console.log(this.catelist)
        },
        //
        async handleChange(){
           this.getParamsData();
        },
        //tab
        handleTabClick(){
           this.getParamsData();
        },
        //
        async getParamsData(){
            //
            if(this.selectedKeys.length!=3){
                this.selectedKeys = [];
                this.manyTableData =[];
                this.onlyTableData = [];
            }
            //console.log(this.selectedKeys)
           
            const {data:res} = await this.$http.get(`categories/${this.cateId}/attributes`,{
                params:{
                     sel:this.activeName
                }
            })
            if(res.meta.status!=200){
                return this.$message.error('');
            }else{
                res.data.forEach(item=>{
                    item.attr_vals =item.attr_vals ?  item.attr_vals.split(' '):[];
                    //
                    item.inputVisible = false;                  
                    //
                    item.inputValue='';
                })
                if(this.activeName=='many'){
                    this.manyTableData = res.data;
                }else{
                    this.onlyTableData = res.data;
                }
            }
        },
        //
        async showDailog(attr_id){
            
            this.adddialogVisible = true;
        },
        ////
        async showeditDailog(attr_id){
            const {data:res} = await this.$http.get(`categories/${this.cateId}/attributes/${attr_id}`,
            {params:{attr_sel:this.activeName}})
                if(res.meta.status!=200) return this.$message.error('');
                this.editForm = res.data;
                 this.editdialogVisible = true;  
        },
        //
        DailogClosed(){
            this.$refs.addFormRef.resetFields();
        },
        //
        editDailogClosed(){
             this.$refs.editFormRef.resetFields();
        },
        //
        addParams(){
            this.$refs.addFormRef.validate(async valid=>{
                if(!valid) return this.$message.info('');
                const {data:res} = await this.$http.post(`categories/${this.cateId}/attributes`,{
                    attr_name:this.addForm.attr_name,
                    attr_sel:this.activeName
                })
                if(res.meta.status!=201) return this.$message.error('');
                this.$message.success('');
                this.getParamsData();
                this.adddialogVisible = false;
            })
        },
        //
        editParams(){
            this.$refs.editFormRef.validate(async valid=>{
                if(!valid) return this.$message.info('');
                const {data:res} = await this.$http.put(`categories/${this.cateId}/attributes/${this.editForm.attr_id}`,{
                    attr_name:this.editForm.attr_name,
                    attr_sel:this.activeName
                })
                if(res.meta.status!=200) return this.$message.error('');
                this.$message.success('');
                this.getParamsData();
                this.editdialogVisible = false;
            })
        },
        //
        async removeParams(attr_id){
             const result = await this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                 type: 'warning'
            }).catch(err=>err)
            if(result=='cancel')  return this.$message.info('');
            const {data:res} = await this.$http.delete(`categories/${this.cateId}/attributes/${attr_id}`)
                if(res.meta.status!=200) return this.$message.error('');
                this.$message.success('');
                this.getParamsData();
        },
        //
        async handleInputConfirm(row){
            //
            if(row.inputValue.trim().length==0){
                row.inputValue='';
                row.inputVisible = false;
                return;
            }
          // console.log('ok'); 
          row.attr_vals.push(row.inputValue.trim());
          row.inputValue='';
           row.inputVisible = false;
           this.saveAttrs(row); 
        },
        //
        async saveAttrs(row){
            const {data:res} = await this.$http.put(`categories/${this.cateId}/attributes/${row.attr_id}`,{
                    attr_name:row.attr_name,
                    attr_sel:this.activeName,
                    attr_vals:row.attr_vals.join(' ')
                })
                if(res.meta.status!=200) return this.$message.error('');
                this.$message.success('');
        },
        //
        showInput(row){
            row.inputVisible = true;
            //
            //$nextTick
             this.$nextTick(_ => {
                this.$refs.saveTagInput.$refs.input.focus();
            });
        },
        //
        removeTag(i,row){
            row.attr_vals.splice(i,1);
            this.saveAttrs(row);
        }
    },
    computed:{
        isOk(){
            if(this.selectedKeys.length!=3){
                return true
            }else{
                return false
            }
        },
        cateId(){
            if(this.selectedKeys.length==3){
                return this.selectedKeys[2]
            }else{
                return null
            }
        },
        titleText(){
            if(this.activeName=='many'){
                return ''
            }else{
                return '' 
            }
        }
    }
}
</script>

<style lang="less" scoped>
.cat_opt{
    margin:15px 0;
}
.el-tag{
    margin:0 15px;
}
.input-new-tag{
    width: 100px;
}
</style>