<template>
    <div>
         <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <!--  -->
        <el-card>
            <el-row>
                <el-col :span="8">
                    <el-input placeholder="">
                        <el-button slot="append" icon="el-icon-search"></el-button>
                    </el-input>
                </el-col>
            </el-row>
            <!-- // -->
            <el-table :data="orderlist" border stripe>
                <el-table-column type="index"></el-table-column>
                <el-table-column label="" prop="order_number"></el-table-column>
                <el-table-column width="95px" label="" prop="order_price"></el-table-column>
                <el-table-column width="95px" label="" prop="pay_status">
                    <template slot-scope="scope">
                        <el-tag type="success" v-if="scope.row.pay_status=='1'"></el-tag>
                        <el-tag type="danger"></el-tag>
                    </template>
                </el-table-column>
                <el-table-column width="95px" label="" prop="is_send"></el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        {{scope.row.create_time | dateFormat}}
                    </template>
                </el-table-column>
                <el-table-column label="">
                    <template >
                        <el-button @click="showBox" size="mini" type="primary" icon="el-icon-edit"></el-button>
                        <el-button @click="showprocessBox" size="mini" type="success" icon="el-icon-location"></el-button>
                    </template>
                </el-table-column>
            </el-table>
            <!--  -->
             <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="queryinfo.pagenum"
                :page-sizes="[3, 5, 10, 20]"
                :page-size="queryinfo.pagesize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="total">
            </el-pagination>
            <!--  -->
            <el-dialog title=""
                :visible.sync="addressVisible"
                width="30%" @close="formclose">
                <el-form :model="addForm" :rules="addFormrules" ref="addFormRef" label-width="100px" >
                    <el-form-item label="/" prop="address1">
                    <el-cascader  expand-trigger="hover" :options="citydata" v-model="addForm.address1"></el-cascader>
                    </el-form-item>
                    <el-form-item label="" prop="address2">
                        <el-input v-model="addForm.address2"></el-input>
                    </el-form-item>
                 </el-form>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="addressVisible = false"> </el-button>
                    <el-button type="primary" @click="addressVisible = false"> </el-button>
                </span>
            </el-dialog>

            <!--  -->
            <el-dialog
                title=""
                :visible.sync="processVisible"
                width="30%">
                <!--  -->
                <el-timeline >
                    <el-timeline-item
                    v-for="(activity, index) in processlist"
                    :key="index"
                    :timestamp="activity.time">
                    {{activity.context}}
                    </el-timeline-item>
                </el-timeline>
            </el-dialog>
        </el-card>
    </div>
</template>

<script>
import citydata from './citydata.js'
export default {
    data(){
        return{
            queryinfo:{
                query:'',
                pagenum:1,
                pagesize:10
            },
            total:0,
            orderlist:[],
            addressVisible:false,
            addForm:{
                address1:'',
                address2:''
            },
            addFormrules:{
                address1:[
                     { required: true, message: '', trigger: 'blur' },
                ],
                address2:[
                     { required: true, message: '', trigger: 'blur' },
                ]
            },
            //
            citydata,
            // 
            processVisible:false,
            //
            processlist:[]
        }
    },
    created(){
        this.getOrderList()
    },
    methods:{
        //
        async getOrderList(){
            const{data:res} = await this.$http.get('orders',{
                params:this.queryinfo
            });
            if(res.meta.status!=200) return this.$message.error('');
            this.orderlist = res.data.goods;
            this.total = res.data.total
            console.log(this.orderlist);
        },
        handleSizeChange(newSize){
            this.queryinfo.pagesize=newSize;
            this.getOrderList();
        },
        handleCurrentChange(newpage){
            this.queryinfo.pagenum=newpage;
            this.getOrderList();
        },
        //
        showBox(){
            this.addressVisible = true;
        },
        // 
        formclose(){
            this.$refs.addFormRef.resetFields();
        },
        //
        async showprocessBox(){
             const{data:res} = await this.$http.get('/kuaidi/1106975712662');
            if(res.meta.status!=200) return this.$message.error('');
            this.processlist = res.data;
            this.processVisible = true;
            console.log(this.processlist);
        }
    }
}
</script>

<style lang="less" scoped>
@import '../../plugins/timeline/timeline.css';
@import '../../plugins/timeline-item/timeline-item.css';
.el-cascader{
    width: 100%;
}
</style>