<template>
    <div>
        <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <!--  -->
        <el-card>
            <el-row :gutter="20">
                <el-col :span="8">
                    <el-input placeholder="" clearable v-model="queryinfo.query" @clear="getGoodlist">
                        <el-button slot="append"  icon="el-icon-search" @click="getGoodlist"></el-button>
                    </el-input>
                </el-col>
                <el-col :span="4">
                    <el-button type="primary" @click="goAddpage"></el-button>
                </el-col>
            </el-row>
            <!--  -->
            <el-table :data="goodslist" border stripe >
                <el-table-column type="index"></el-table-column>
                <el-table-column label="" prop="goods_name"></el-table-column>
                <el-table-column label="()" prop="goods_price" width="95px"></el-table-column>
                <el-table-column label="" prop="goods_weight" width="70px"></el-table-column>
                <el-table-column label=""  width="140px">
                    <template slot-scope="scope">
                        {{scope.row.add_time | dateFormat}}
                    </template>
                </el-table-column>
                <el-table-column label="" width="130px">
                    <template slot-scope="scope">
                        <el-button type="primary" icon="el-icon-edit" size="mini"></el-button>
                        <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeById(scope.row.goods_id)"></el-button>
                    </template>
                </el-table-column>    
            </el-table>
            <!--  -->
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="queryinfo.pagenum"
                :page-sizes="[5, 10, 15, 20]"
                :page-size="queryinfo.pagesize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="total" background>
            </el-pagination>
        </el-card>
    </div>
</template>

<script>
export default {
    data(){
        return{
            //
            queryinfo:{
                query:'',
                pagenum:1,
                pagesize:10
            },
            //
            goodslist:[],
            //
            total:0,
        }
    },
    created(){
        this.getGoodlist();
    },
    methods:{
        //
        async getGoodlist(){
            const {data:res} = await this.$http.get('goods',
            {params:this.queryinfo})
            if(res.meta.status!=200) return this.$message.error('');
            console.log(res.data);
            this.goodslist = res.data.goods;
            this.total = res.data.total;

        },
        handleSizeChange(newSize){
            this.queryinfo.pagesize = newSize;
            this.getGoodlist();
        },
        handleCurrentChange(newPage){
            this.queryinfo.pagenum = newPage;
            this.getGoodlist();
        },
        //
        async removeById(id){
          const result =  await this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).catch(err=>err)
            if(result!='confirm') return this.$message.info('');
             const {data:res} = await this.$http.delete('goods/'+id)
            if(res.meta.status!=200) return this.$message.error('');
            this.$message.success('');
            this.getGoodlist();
        },
        //
        goAddpage(){
            this.$router.push('/goods/add')
        }
    }
}
</script>

<style lang="less" scoped>

</style>