<template>
    <div class="login_contanier">
        <div class="login_box">
            <!--  -->
            <div class="avator_box">
                <img src="../assets/logo.png" alt="">
            </div>
            <!--  -->
            <el-form ref="loginFormRef" :model="loginForm" :rules="loginFormRules" label-width="0px" class="login_form">
                <!--  -->
                <el-form-item label="" prop="username">
                    <el-input v-model="loginForm.username" prefix-icon="iconfont icon-user"></el-input>
                </el-form-item>
                <!--  -->
                <el-form-item label="" prop="password">
                    <el-input v-model="loginForm.password" type="password" prefix-icon="iconfont icon-3702mima"></el-input>
                </el-form-item>
                <!--  -->
                <el-form-item label="" class="btns">
                    <el-button type="primary" @click="login"></el-button>
                    <el-button type="info" @click="reset"></el-button>
                </el-form-item>
            </el-form>
        </div>
    </div>
</template>

<script>
export default { 
    data () { 
        return { 
            //
            loginForm: { 
                username: 'admin', 
                password: '123456' 
            } ,
            //
            loginFormRules :{
                //
                username:[
                    { required: true, message: '', trigger: 'blur' },
                    { min: 3, max: 10, message: ' 3  10 ', trigger: 'blur' }
                ],
                //
                password:[
                    { required: true, message: '', trigger: 'blur' },
                    { min: 6, max: 16, message: ' 6  16 ', trigger: 'blur' }
                ]
            }
        }
    } ,
    methods:{
        reset:function(){
            //vue
            this.$refs.loginFormRef.resetFields();
            //console.log(this)
        },
        login(){
            //
            this.$refs.loginFormRef.validate( async valid=>{
                //true,false
               // console.log(valid);
               if(!valid) return;
               const {data:res} = await this.$http.post('login',this.loginForm);
               if(res.meta.status!=200)  return this.$message.error('');
               this.$message.success('')
                // 1.token,sessionStirage
                // 1.1API
                // 1.2tokentakensessionStorage
                window.sessionStorage.setItem('token',res.data.token);
                // 2. /home
                this.$router.push('/home');
            })
        }
    }
}
</script>

<style lang="less" scoped>
.login_contanier{
    background-color: #2b4b6b;
    width: 100%;
    height: 100%;
}
.login_box{
    width: 450px;
    height: 300px;
    background-color: #fff;
    border-radius: 3px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    .avator_box{
        width: 130px;
        height: 130px;
        border:1px solid #eee;
        border-radius: 50%;
        padding: 10px;
        box-shadow: 0 0 10px  #ddd;
        position: absolute;
        left: 50%;
        transform: translate(-50%,-50%);
        background-color: #fff;
        img{
            width: 100%;
            height: 100%;
            border-radius: 50%;
            background-color: #eee;
        }
    }
}
.btns{
    display: flex;
    justify-content: flex-end;
}
.login_form{
    position: absolute;
    bottom: 0;
    width: 100%;
    padding: 0 10px;
    box-sizing: border-box;
}
</style>
