<template>
    <div id="app">
        <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <!--  -->
        <el-card>
            <el-button type="primary" @click="showaddDailog"></el-button>
            <!--  -->
            <tree-table class="treeTable" :data="catelist" :columns="columns" :selection-type="false"
                :expand-type="false" show-index index-text="#" border>
                <!--  -->
                <template slot="isok" slot-scope="scope">
                <i class="el-icon-success" style="color:lightgreen" 
                v-if="scope.row.cat_deleted===false"></i>
                <i class="el-icon-error" style="color:red" 
                v-else></i>
                 </template>
                <!--  -->
                <template slot="order" slot-scope="scope">
                <el-tag v-if="scope.row.cat_level==0" size="mini"></el-tag>
                <el-tag v-else-if="scope.row.cat_level==1" type="success" size="mini"></el-tag>
                <el-tag v-else type="warning" size="mini"></el-tag>
                </template>
                <!--  -->
                <template slot="opt" slot-scope="scope">
                <el-button :hidden="scope" type="primary" icon="el-icon-edit" size="mini"></el-button>
                <el-button type="danger" icon="el-icon-delete" size="mini"></el-button>
                </template>
            </tree-table>
            <!--  -->
             <el-pagination @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="queryinfo.pagenum"
                :page-sizes="[3, 5, 10, 15]"
                :page-size="queryinfo.pagesize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="total">
            </el-pagination>
        </el-card>
        <!--  -->
        <el-dialog title="" :visible.sync="adddialogVisible"
            width="30%" @close="DailogClosed">
            <el-form :model="addForm" :rules="addFormrules" ref="addFormRef" label-width="100px">
                <el-form-item label="" prop="cat_name">
                    <el-input v-model="addForm.cat_name"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <!-- options -->
                    <!-- props -->
                    <el-cascader v-model="selectedKeys" :options="parrentcatelist"
                    :props="cascaderProps" props.expandTrigger="hover"
                    @change="selecthandleChange" clearable props.checkStrictly ></el-cascader>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="adddialogVisible = false"> </el-button>
                <el-button type="primary" @click="addCate"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
export default {
    data(){
        return{
            //
            queryinfo:{
                type:3,
                pagenum:1,  //
                pagesize:3  //
            },
            catelist:[],   //
            total: 1,      //
            //table
            columns:[
                {
                label:'',
                prop:'cat_name'
                },{
                label:'',
                //
                type:"template",
                //
                template:"isok"
                },{
                label:'',
                //
                type:"template",
                //
                template:"order"
                },{
                label:'',
                //
                type:"template",
                //
                template:"opt"
                }
            ],
            //
            adddialogVisible: false,
            //
            addForm:{
                cat_name:'',
                //ID
                cat_pid:0,
                //
                cat_level:0
            },
            //
            addFormrules:{
                cat_name:[
                     { required: true, message: '', trigger: 'blur' },
                ]
            },
            //
            parrentcatelist:[],
            //
            cascaderProps:{
                value:'cat_id',
                label:'cat_name',
                children:'children'
            },
            selectedKeys:[]
        }
    },
    created(){
        this.getCatelist();
    },
    methods:{
        async getCatelist(){
            const{data:res} = await this.$http.get('categories',{params:this.queryinfo});
            if(res.meta.status!=200) return this.$message.error('');
            this.catelist = res.data.result;
            this.total = res.data.total;
           // console.log(res);
        },
        //
        handleSizeChange(newsize){
            this.queryinfo.pagesize = newsize;
            this.getCatelist();
        },
        //
        handleCurrentChange(newpagenum){
            this.queryinfo.pagenum = newpagenum;
            this.getCatelist();
        },
        //
        showaddDailog(){
            this.getparentCateList();
            this.adddialogVisible = true;
        },
        //
        async getparentCateList(){
            const {data:res} = await this.$http.get('categories',{params:{
                type:2
            }});
            if(res.meta.status!=200){
                return this.$message.error('');
            }
            this.parrentcatelist = res.data;
            //console.log(this.parrentcatelist);
        },
        selecthandleChange(){
            //console.log(this.selectedKeys);
            //selsctedKeys0
            //console.log(this.selectedKeys)
            if(this.selectedKeys.length>0){
                //ID
                this.addForm.cat_pid = this.selectedKeys[this.selectedKeys.length-1]
                //
                this.addForm.cat_level = this.selectedKeys.length;
                return;
            }else{
                 //ID
                this.addForm.cat_pid = 0;
                //
                this.addForm.cat_level = 0;
            }
        },
        //
        addCate(){
          this.$refs.addFormRef.validate(async valid=>{
              if(!valid){
                  return this.$message.info('');
              }else{
                 // console.log(this.addForm)
                  const {data:res} = await this.$http.post('categories',this.addForm
                  );
                  //console.log(res);
                  if(res.meta.status!=201){
                      return this.$message.error('');
                  }
                  this.$message.success('');
                  this.getCatelist();
                  this.adddialogVisible = false
              }
          })

            
        },
        //
        DailogClosed(){
            this.$refs.addFormRef.resetFields();
            this.selectedKeys = [];
            this.addForm.cat_level=0;
            this.addForm.cat_pid = 0;
        }
       
    }
}
</script>

<style lang="less" scoped>
.treeTable{
    margin-top: 15px;
}
.el-cascader{
    width: 100%;
}
</style>