<template>
    <div>
        <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <el-card>
          <el-button type="primary"></el-button>
          <el-table :data="roleslist" border stripe >
              <el-table-column type="expand">
                  <template slot-scope="scope">
                    <el-row  :class="['bdborder',i1==0?'topborder':'','vcenter']" v-for="(item1, i1) in scope.row.children" :key="item1.id">
                        <!--  -->
                        <el-col :span="5">
                            <el-tag closable @close="removeRights(scope.row,item1.id)">{{item1.authName}}</el-tag>
                            <i class="el-icon-caret-right"></i>
                        </el-col>
                        <!--  -->
                        <el-col :span="19">
                            <el-row :class="[i2==0?'':'topborder','vcenter']" v-for="(item2,i2) in item1.children" :key="item2.id">
                                <el-col :span="6">
                                    <el-tag type="success" closable @close="removeRights(scope.row,item2.id)">{{item2.authName}}</el-tag>
                                    <i class="el-icon-caret-right"></i>
                                </el-col > 
                                <!--   -->
                                <el-col :span="18">
                                    <el-tag type="warning" closable @close="removeRights(scope.row,item3.id)"  v-for="item3 in item2.children" :key="item3.id">{{item3.authName}}</el-tag>
                                </el-col>
                            </el-row>
                        </el-col>
                    </el-row>
                  </template>
              </el-table-column>
              <el-table-column type="index"></el-table-column>
              <el-table-column label="" prop="roleName"></el-table-column>
              <el-table-column label="" prop="roleDesc"></el-table-column>
              <el-table-column label="">
                  <template slot-scope="scope">
                        <el-button-group>
                            <el-button size="mini" type="primary" :id="scope.row.id" icon="el-icon-edit"></el-button>
                            <el-button size="mini" type="danger" icon="el-icon-delete"></el-button>
                            <el-button size="mini" @click="showDailog(scope.row)" type="warning" icon="el-icon-setting"></el-button>
                        </el-button-group>
                  </template>
              </el-table-column>
          </el-table>
        </el-card>

        <!--  -->
        <el-dialog title="" :visible.sync="dialogVisible" width="30%"  @close="closedDailog">
            <!--  -->
            <el-tree ref="treeref" :data="rightslist" :props="treeProps" show-checkbox node-key="id" 
            default-expand-all highlight-current :default-checked-keys="defKeys"></el-tree>
        <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false"> </el-button>
            <el-button type="primary" @click="subDailog"> </el-button>
        </span>
        </el-dialog>
    </div>
</template>

<script>
export default {
    data(){
        return{
            //
            roleslist:[],
            //
            dialogVisible: false,
            rightslist:[],
            treeProps:{
                children:'children',
                label:'authName'
            },
            //
            defKeys:[],
            roleId:0
        }
    },
    created(){
        this.getRoleslist();
    },
    methods:{
        async getRoleslist(){
            const {data:res} = await this.$http.get('roles');
            if(res.meta.status!=200) return this.$message.error('');

            this.roleslist = res.data;
        },
        //
        async removeRights(role,id){
        const result =  await this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).catch(err=>err)
            if(result!=='confirm') return this.$message.info('');
            const {data:res} = await this.$http.delete(`roles/${role.id}/rights/${id}`)
            console.log(res);
            if(res.meta.status!==200){
                return this.$message.error('');
            }
             role.children = res.data;
        },
        //
        async showDailog(node){
            this.roleId = node.id;
            const {data:res} = await this.$http.get('rights/tree')
            if(res.meta.status!=200){
                return this.$message.info('')
            }
            this.rightslist = res.data;
            //id
            this.getLeafKeys(node,this.defKeys)
            this.dialogVisible = true;
        },
        // iddefKeys
        getLeafKeys(node,arr){
            if(!node.children){
             return arr.push(node.id)
            }
            node.children.forEach(element => {
                this.getLeafKeys(element,arr);
            });
        },
        //
        closedDailog(){
            this.defKeys = [];
        },
        async subDailog(){
            const keys = [
                ...this.$refs.treeref.getCheckedKeys(),
                ...this.$refs.treeref.getHalfCheckedKeys()
            ]
            const str = keys.join(',')
           const {data:res} = await this.$http.post(`roles/${this.roleId}/rights`,{
               rids:str
           })
           console.log(res);
           if(res.meta.status!=200){
               return this.$message.error('')
           }
            this.$message.success('');
            this.getRoleslist();
            this.dialogVisible = false;
        }
    }
}
</script>

<style lang="less" scoped>
.el-tag{
    margin: 7px;
}
.topborder{
    border-top: 1px solid #eee;
}
.bdborder{
    border-bottom: 1px solid #eee;
}
.vcenter{
    display: flex;
    align-items: center;
}
</style>