<template>
    <el-container class="home_container">
        <!--  -->
        <el-header>
            <div>
                <img src="../assets/heima.png" alt="">
                <span></span>
            </div>
            <el-button type="info" @click="logout"></el-button>
        </el-header>
        <el-container>
            <!--  -->
            <el-aside :width="isCollapse?'64px':'200px'">
                <div class="toggle-button" @click="toggleCollapse">|||</div>
                <el-menu background-color="#333744" text-color="#fff" active-text-color="#409eff" 
                unique-opened :collapse="isCollapse" :collapse-transition="false" router :default-active="activepath">
                    <!--  -->
                    <el-submenu :index="item.id + ''" v-for="item in menulist" :key="item.id">
                        <!--  -->
                        <template slot="title">
                            <!--  -->
                        <i :class="iconObj[item.id]"></i>
                        <!--  -->
                        <span>{{item.authName}}</span>
                        </template>
                        <!--  -->
                        <el-menu-item :index="'/'+subitem.path" @click="avtiveNavState('/'+subitem.path)" v-for="subitem in item.children" :key="subitem.id">
                            <template slot="title">
                            <i class="el-icon-menu"></i>
                            <span>{{subitem.authName}}</span>
                            </template>
                        </el-menu-item>
                    </el-submenu>
                </el-menu>
            </el-aside>
            <!--  -->
            <el-main>
                <router-view></router-view>
            </el-main>
        </el-container>
    </el-container>
</template>

<script>
export default {
    data() {
        return { 
            //
            menulist: [],
            //
            iconObj:{
                '125':'iconfont icon-users',
                '103':'iconfont icon-tijikongjian',
                '101':'iconfont icon-shangpin',
                '102':'iconfont icon-danju',
                '145':'iconfont icon-baobiao',
            },
            isCollapse:false,
            //
            activepath:''
        }
    },
    //
    created(){
        this.getMenuList()
        this.activepath = window.sessionStorage.getItem('activepath');
    },
    methods:{
        //
        logout(){
            window.sessionStorage.clear();
            this.$router.push('/login');
        },
        async getMenuList(){
            const {data: res} = await this.$http.get('menus');
            if (res.meta.status!=200) return this.$message.error(res.meta.msg)
            this.menulist = res.data;
           // console.log(this.menulist);
        },
        //
        toggleCollapse(){
            this.isCollapse = ! this.isCollapse;
        },
        //
        avtiveNavState(activepath){
            window.sessionStorage.setItem('activepath',activepath);
            this.activepath = activepath;
        }
    }
}
</script>

<style lang="less" scoped>
.home_container{
    height: 100%;
}
.el-header{
    background-color: #373d41;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 0;
    color: #fff;
    font-size: 20px;
    >div{
        display: flex;
        align-items: center;
        span{
            margin-left: 15px;
        }
    }
}
.el-aside{
    background-color: #333744;
    .el-menu{
        border:none;
    }
    //
    .toggle-button{
        background-color: #4a5064;
        font-size: 10px;
        line-height: 24px;
        color:#fff;
        text-align: center;
        letter-spacing: 0.4em;
        cursor: pointer;
    }
}
.el-maim{
    background-color: #eaedf1;
}
.iconfont{
    margin-right: 10px;
}
</style>