<template>
    <div>
       <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <!--  -->
        <el-card>
            <el-alert title="" type="info" center 
              show-icon :closable="false"></el-alert>

              <!--  -->
            <el-steps :space="200" :active="activeIndex-0" align-center finish-status="success">
                <el-step title=""></el-step>
                <el-step title=""></el-step>
                <el-step title=""></el-step>
                <el-step title=""></el-step>
                <el-step title=""></el-step>
                <el-step title=""></el-step>
            </el-steps>
            <!-- tab -->
            <el-form :model="addForm" :rules="addFormrules" ref="addFormRef" label-width="100px" label-position="top">
             <el-tabs v-model="activeIndex" :tab-position="'left'" 
             :before-leave="beforeLeave" @tab-click="tabClick">
                <el-tab-pane label="" name="0">
                    <el-form-item label="" prop="goods_name">
                        <el-input v-model="addForm.goods_name"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="goods_price">
                        <el-input type="number" v-model="addForm.goods_price"></el-input>
                    </el-form-item>
                    <el-form-item type="number" label="" prop="goods_weight">
                        <el-input v-model="addForm.goods_weight"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="goods_number">
                        <el-input v-model="addForm.goods_number"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="goods_cat">
                         <el-cascader
                        v-model="addForm.goods_cat"
                        :options="catelist"
                        expand-trigger="hover"
                        :props="caseProps"
                        @change="handleChange"></el-cascader>
                    </el-form-item>
                </el-tab-pane>
                <el-tab-pane label="" name="1">
                    <el-form-item :label="item.attr_name" v-for="item in manyTableData" :key="item.attr_id">
                        <el-checkbox-group v-model="item.attr_vals">
                            <el-checkbox border :label="cb" v-for="(cb,i) in item.attr_vals" :key="i"></el-checkbox>
                        </el-checkbox-group>
                    </el-form-item>
                </el-tab-pane>
                <el-tab-pane label="" name="2">
                    <el-form-item :label="item.attr_name" v-for="item in onlyTableData" :key="item.attr_id">
                        <el-input v-model="item.attr_vals"></el-input>
                    </el-form-item>
                </el-tab-pane>
                <el-tab-pane label="" name="3">
                    <el-upload :action="uploadURL"
                        :on-preview="handlePreview"
                        :on-remove="handleRemove"
                        list-type="picture"
                        :headers="headersObj"
                        :on-success="handleSuccess">
                        <el-button size="small" type="primary"></el-button>
                        <div slot="tip" class="el-upload__tip">jpg/png500kb</div>
                    </el-upload>
                </el-tab-pane>
                <el-tab-pane label="" name="4">
                    <!--  -->
                     <quill-editor v-model="addForm.goods_introduce"></quill-editor>
                     <!--  -->
                     <el-button type="primary" class="btn" @click="add"></el-button>
                </el-tab-pane>
            </el-tabs>
            </el-form>
        </el-card>
        <el-dialog title="" :visible.sync="previewdialogVisible"
            width="50%">
            <img :src="previewPath" class="previewimg" alt="">
        </el-dialog>
    </div>
</template>

<script>
import _ from 'lodash'
export default {
    data(){
        return{
            activeIndex:'0',
            //
            addForm:{
                goods_name:'',
                goods_price:0,
                goods_weight:0,
                goods_number:0,
                //
                goods_cat:[],
                //
                pics:[],
                //
                goods_introduce:'',
                //
                attrs:[]
            },
            //
            addFormrules:{
                goods_name:[{ 
                    required: true, message: '', trigger: 'blur' 
                    }],
                goods_price:[{ 
                    required: true, message: '', trigger: 'blur' 
                    }],
                goods_weight:[{ 
                    required: true, message: '', trigger: 'blur' 
                    }],
                goods_numbee:[{ 
                    required: true, message: '', trigger: 'blur' 
                    }],
                goods_cat:[{ 
                    required: true, message: '', trigger: 'blur' 
                    }],

            },
            //
            catelist:[],
            caseProps:{
                label:'cat_name',
                value:'cat_id',
                children:'children'
            },
            //
            manyTableData:[],
            onlyTableData:[],
            //
            uploadURL:'http://127.0.0.1:8888/api/private/v1/upload',
            //
            headersObj:{
                Authorization:window.sessionStorage.getItem('token')
            },
            //
            previewPath:'',
            previewdialogVisible:false

        }
    },
    created(){
        this.getCateList()
    },
    methods:{
       async getCateList(){
            const{data:res} = await this.$http.get('categories');
            if(res.meta.status!=200) return this.$message.error('');
            this.catelist = res.data;
       } ,
       handleChange(){
           //console.log(this.addForm.goods_cat)
           if(this.addForm.goods_cat.length!==3){
               this.addForm.goods_cat = []
           }
       },
       //activeNametanbname
       beforeLeave(activeName,oldactiveName){
           if(oldactiveName=='0'&&this.addForm.goods_cat.length!==3){
               this.$message.error('');
               return false;
           }
       },
       //tab
       async tabClick(){
           //
           if(this.activeIndex=='1'){
                const{data:res} = await this.$http.get(`categories/${this.cateId}/attributes`,
                {params:{sel:"many"}});
                if(res.meta.status!=200) return this.$message.error('');
                res.data.forEach(item=>{
                    item.attr_vals=item.attr_vals.length==0?[]:item.attr_vals.split(' ')
                })
                this.manyTableData = res.data;
               // console.log(this.manyTableData);
               //
           }else if(this.activeIndex=='2'){
                const{data:res} = await this.$http.get(`categories/${this.cateId}/attributes`,
                {params:{sel:"only"}});
                if(res.meta.status!=200) return this.$message.error('');
                // res.data.forEach(item=>{
                //     item.attr_vals=item.attr_vals.length==0?[]:item.attr_vals.split(' ')
                // })
                this.onlyTableData = res.data;
                //console.log(this.onlyTableData)
           }
        
       },
       //
       handlePreview(file){
           this.previewPath = file.response.data.url;
           this.previewdialogVisible = true;
       },
       //
       handleRemove(file){
           //1.
           const filepath = file.response.data.tmp_path;
           //
           const i = this.addForm.pics.findIndex(item=>
                item.pic==filepath
           )
           //
           this.addForm.pics.splice(i,1);
       },
       //
       handleSuccess(response){
           //console.log(response);
           const picinfo ={pic:response.data.tmp_path};
           this.addForm.pics.push(picinfo);
           //console.log(this.addForm);
       },
       //
       add(){
           //console.log(this.addForm)
           this.$refs.addFormRef.validate(async valid=>{
               if(!valid) return this.$message.error('')
                // lodsh cloneDeep(obj) 
                const form = _.cloneDeep(this.addForm);
                form.goods_cat = form.goods_cat.join(',');
                //
                this.manyTableData.forEach(item=>{
                   const newInfo = {
                        attr_id:item.attr_id,
                        attr_value:item.attr_vals.join(' ')}
                    this.addForm.attrs.push(newInfo);
                })
                //
                this.onlyTableData.forEach(item=>{
                   const newInfo = {
                        attr_id:item.attr_id,
                        attr_value:item.attr_vals}
                    this.addForm.attrs.push(newInfo);
                })
                form.attrs = this.addForm.attrs;
                //
                 const{data:res} = await this.$http.post('goods',form);
                if(res.meta.status!=201) return this.$message.error('');
                this.$router.push('/goods');
           })
       }
    },
    computed:{
            cateId(){
              if(this.addForm.goods_cat.length==3){
                        return this.addForm.goods_cat[2]
                    }else{
                        return null;
                    }
                }
        }
}
</script>

<style lang="less" scoped>
.el-checkbox{
    margin: 0 10px 0 0!important
}
.previewimg{
    width: 100%;
}
.btn{
    margin-top: 15px ;
}
</style>